export default class App extends Component {
    
    state = {
        mainClicked : false
    }
    
    render() {
        const { mainClicked, clickedElem, hoveredElem } = this.state;
        return (
            <div className="app">
                <div className={'title'}>
                    Andr√©s Movilla
                </div>
                <div className={'container' + (mainClicked ? ' force-hover' : '')}>
                    
                    <div
                        className={'face'}
                        // onClick={() => this.setState({ mainClicked : !mainClicked })}
                    >
                        <img src={icon} alt={''} />
                    </div>
                    
                    {Object.keys(info).map(item => {
                        return (
                            <Item
                                icon={[ info[item].iconLibrary, item ]}
                                clickedElem={clickedElem}
                                hoverCallback={( elem ) => this.setState({ hoveredElem : elem })}
                                clickCallback={( elem ) => this.setState(
                                    { clickedElem : this.state.clicked === elem ? '' : elem })}
                            />
                        )
                    })}
                </div>
                <Info hoveredElem={hoveredElem} clickedElem={clickedElem} />
            </div>
        );
    }
}

function Item( { icon, hoverCallback, clickCallback, clickedElem } ) {
    let callbackVal = icon;
    if ( typeof (icon) === 'object' ) {
        callbackVal = icon[1];
    }
    return (
        <div
            className={'item-container' + (callbackVal === clickedElem ? ' force-hover' : '')}
            // onMouseEnter={() => hoverCallback(callbackVal)}
            // onMouseLeave={() => hoverCallback('')}
            onClick={() => clickCallback(callbackVal)}
        >
            <div className={'circle item'}>
                <FontAwesomeIcon icon={icon} />
            </div>
        </div>
    )
}

function Info( { hoveredElem, clickedElem } ) {
    let elem = clickedElem;
    if ( !clickedElem ) {
        elem = hoveredElem;
    }
    if ( !elem ) {
        return (<div className={'description hide'}>
        
        </div>)
    }
    return (
        <div className={'description visible'}>
            <div className={'header'}>
                {info[elem].header}
            </div>
            {info[elem].body}
        </div>
    )
}

function EduItem( { date, name, field } ) {
    return (<div className={'education-item'}>
        <div className={'name'}>{name}</div>
        <div className={'secondary-row'}>
            <div className={'date'}>{date}</div>
            <div className={'field'}>{field}</div>
        </div>
    </div>)
}

function ExpItem( { name, client, date } ) {
    return (<div className={'experience-item'}>
        <div className={'name'}>{name}</div>
        <div className={'secondary-row'}>
            <div className={'date'}>{date}</div>
            <div className={'field'}>{client}</div>
        </div>
    </div>)
}
